module.exports = "class TabExample extends React.Component {\r\n  constructor() {\r\n    super();\r\n    this.state = {\r\n      selectedTab: 'a',\r\n    };\r\n  }\r\n\r\n  selectTabA() {\r\n    this.setState({ selectedTab: 'a' });\r\n  }\r\n\r\n  selectTabB() {\r\n    this.setState({ selectedTab: 'b' });\r\n  }\r\n\r\n  render() {\r\n    return <ConnectedComponent render={(props) => {\r\n      const { context } = props;\r\n      const { rem, font } = context;\r\n      const { sizes, weights } = font;\r\n\r\n      const styles = {\r\n        header: { ...sizes.title, ...weights.semibold },\r\n        section: { ...sizes.title2, marginTop: rem(1.4), marginBottom: rem(1.4) }\r\n      }\r\n\r\n      return <Panel>\r\n        <PanelHeader>\r\n          <div style={styles.header}>Tabs</div>\r\n        </PanelHeader>\r\n        <PanelBody>\r\n          <div style={styles.section}></div>\r\n          <Tab\r\n            selectedTabId={this.state.selectedTab}\r\n            tabs={[\r\n              {\r\n                text: 'Tab A',\r\n                onSelect: () => this.selectTabA(),\r\n                id: 'a',\r\n              },\r\n              {\r\n                text: 'Tab B',\r\n                onSelect: () => this.selectTabB(),\r\n                id: 'b',\r\n              }\r\n            ]}\r\n          />\r\n        </PanelBody>\r\n        <PanelFooter>\r\n        </PanelFooter>\r\n      </Panel>;\r\n    }} />;\r\n  }\r\n}\r\n"


